@font-face {
  font-family: "Droid Sans";
  font-style: normal;
  font-weight: 400;
  font-display: fallback;
  src: url("../fonts/droidsans.woff2") format("woff2"), url("../fonts/droidsans.woff") format("woff"); }

body {
  font-family: "Droid Sans", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #000000; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px; }

.page-header {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding-top: 20px;
  padding-bottom: 300px;
  background-image: url("../img/image_header.png"), linear-gradient(rgba(30, 9, 6, 0.15), rgba(30, 9, 6, 0.15)), linear-gradient(#3f3f3f, #3f3f3f), linear-gradient(#3f3f3f, #3f3f3f), linear-gradient(#3f3f3f, #3f3f3f), linear-gradient(#3f3f3f, #3f3f3f), linear-gradient(#3f3f3f, #3f3f3f), linear-gradient(#3f3f3f, #3f3f3f);
  background-size: 825px 298px, 100% 110px, 100% 4px, 100% 4px, 100% 4px, 100% 4px, 100% 4px, 100% 4px;
  background-position: top 72px right 0, 0 0, 0 134px, 0 148px, 0 162px, 0 176px, 0 190px, 0 204px;
  background-repeat: no-repeat; }

.page-header > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 940px;
  flex-basis: 95%; }

.page-header__logo {
  color: #000000; }
  .page-header__logo:hover, .page-header__logo:focus {
    color: #474747; }
  .page-header__logo:active {
    color: rgba(71, 71, 71, 0.5); }

.page-header__nav {
  margin-right: 35px;
  max-width: 510px; }

.page-header__nav ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.page-header__nav li {
  margin-right: 40px; }

.page-header__nav li:last-child {
  margin-right: 0; }

.page-header__nav a {
  font-family: "Droid Sans", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.03em;
  text-decoration: none;
  color: #000000; }
  .page-header__nav a:hover, .page-header__nav a:focus {
    color: #474747; }
  .page-header__nav a:active {
    color: rgba(71, 71, 71, 0.5); }

.page-header__links {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  max-width: 150px;
  padding-right: 8px;
  padding-bottom: 4px; }

.page-header__links a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 24px;
  margin-right: 15px;
  text-decoration: none;
  color: #000000; }
  .page-header__links a:hover, .page-header__links a:focus {
    color: #474747; }
  .page-header__links a:active {
    color: rgba(71, 71, 71, 0.5); }

.page-header__links a:last-child {
  margin-right: 0; }

.page-main {
  flex: 1 0 auto; }

.page-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding-top: 168px;
  padding-bottom: 60px;
  background-image: linear-gradient(#3f3f3f, #3f3f3f), linear-gradient(#3f3f3f, #3f3f3f), url("../img/image_footer.png");
  background-size: 100% 278px, 100% 6px, 825px 298px;
  background-position: bottom 0 left 0, bottom 288px left 0, bottom 65px left 0;
  background-repeat: no-repeat; }

.page-footer > div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 940px;
  flex-basis: 95%; }

.page-footer h3 {
  margin: 0;
  font-family: "Droid Sans", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.06em;
  color: #ffffff;
  opacity: 0.7; }

.page-footer a {
  color: #ffffff; }
  .page-footer a:hover, .page-footer a:focus {
    color: rgba(255, 255, 255, 0.8); }
  .page-footer a:active {
    color: rgba(255, 255, 255, 0.6); }

.page-footer__left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 145px;
  min-height: 153px;
  margin-top: -12px; }

.page-footer__logo {
  color: #ffffff; }

.page-footer__social-links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none; }

.page-footer__social-links li {
  margin-right: 23px; }

.page-footer__center {
  display: flex;
  justify-content: space-between;
  max-width: 470px;
  margin-left: -37px; }

.page-footer__center div {
  max-width: 145px;
  margin-right: 15px; }

.page-footer__center div:last-child {
  margin-right: 0;
  margin-left: 25px; }

.page-footer__center p {
  margin: 0;
  margin-top: 12px;
  font-family: "Droid Sans", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 12px;
  color: #ffffff; }

.page-footer__center a {
  font-family: "Droid Sans", "Verdana", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 11px;
  letter-spacing: 0.06em;
  color: #ffffff;
  text-decoration: none; }

.page-footer__center ul {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-top: 6px; }

.page-footer__right {
  max-width: 140px;
  padding-top: 13px;
  padding-right: 15px; }

.page-footer__right address,
.page-footer__right p,
.page-footer__right a {
